//inicia funcion
document.addEventListener('DOMContentLoaded', function(){

    carrusel();
    menuMobile();
    darkMode();
})
function darkMode(){
    const seleccionDarkmode =window.matchMedia('(prefers-color-scheme:dark)');
    
    if(seleccionDarkmode.matches){
        document.body.classList.add('darkmode');
    }
    else{
        document.body.classList.remove('darkmode');
    }
    seleccionDarkmode.addEventListener('change' ,function(){
        if(seleccionDarkmode.matches){
            document.body.classList.add('darkmode');
        }
        else{
            document.body.classList.remove('darkmode');
        }
    })
    
    const botonLightMode = document.querySelector('.img-light-btn');
    const botonDarkMode = document.querySelector('.img-dark-btn');
    botonDarkMode.addEventListener('click', function(){
        document.body.classList.add('darkmode'); 
        botonDarkMode.style.visibility = 'hidden';
        botonLightMode.style.visibility ='visible';
    }); 
    botonLightMode.addEventListener('click',function(){
        document.body.classList.remove('darkmode'); 
        botonDarkMode.style.visibility = 'visible';
        botonLightMode.style.visibility ='hidden';
    })

}
function menuMobile(){
    const menu = document.querySelector('.barra');
    menu.addEventListener('click', navegacion);
}

function navegacion(){
    const menuNavegacion = document.querySelector('.nav_principal');

    menuNavegacion.classList.toggle('activar');
}
// carrusel
function carrusel(){
const galeria = document.querySelector(".galeria");
const puntos  = document.querySelectorAll(".punto");


    puntos.forEach( ( soloPunto , i) =>{ 
        

        puntos[i].addEventListener('click', () =>{
            
            let posicion = i;

            
            let calculo = posicion * - 33.3;
        
            galeria.style.transform = `translateX(${calculo}%)`;
    
            puntos.forEach((soloPunto , i)=>{
                
                puntos[i].classList.remove('activo');
            })
            puntos[i].classList.add('activo');
    
        })
       
    })
}
